/* eslint-disable no-useless-concat */
import React, { Component } from "react";
import $ from "jquery";
import { CARS, YEAR, MAKE } from "../car-data";

export default class AutoPart extends Component {
  componentDidMount = () => {
    MAKE.sort();

    const yearSelection = document.getElementById("year");
    const makeSelection = document.getElementById("make");
    const modelSelection = document.getElementById("model");

    for (let year of YEAR) {
      const option = document.createElement("option");
      option.textContent = year;
      option.value = year;
      yearSelection.appendChild(option);
    }

    for (let make of MAKE) {
      const option = document.createElement("option");
      option.textContent = make;
      option.value = make;
      makeSelection.appendChild(option);
    }

    const models = [];
    for (let car of CARS) {
      const model = car.model;
      if (models.findIndex((m) => m == model) == -1) {
        const option = document.createElement("option");
        option.className = "car-model";
        models.push(model);
        option.textContent = model;
        option.value = model;
        modelSelection.appendChild(option);
      }
    }
    const modelOptions = Object.values(document.querySelectorAll(".car-model"));

    function populateModels() {
      const selectedYear = yearSelection.value;
      const selectedMake = makeSelection.value;
      const filtered = CARS.filter(
        (car) => car.year == selectedYear && car.make == selectedMake
      );

      for (let option of modelOptions) {
        if (filtered.find((v) => v.model == option.value)) {
          option.hidden = false;
        } else {
          option.hidden = true;
        }
      }

      if (filtered.length > 0) {
        modelSelection.value = filtered[0].model;
      } else {
        modelSelection.value = "none";
      }
    }

    populateModels();
    yearSelection.onchange = () => populateModels();
    makeSelection.onchange = () => populateModels();

    $(".haks_add").on("click", function() {
      var markup =
        "<div><div class='form-row'><div class='form-group col-md-12 col-xs-12 col-sm-12'><input type='text' class='form-control' id='partname' name='entry.1660104041' placeholder='Part Name'></div></div></div>";
      var form = $("#haks_add_field");
      form.append(markup);
    });
    $("#myForm").submit(function(event) {
      var submitted = true;
      if (submitted) {
        event.preventDefault();
        var email = document.getElementById("email").value;
        var make = document.getElementById("make").value;
        var model = document.getElementById("model").value;
        var year = document.getElementById("year").value;
        var partname = $("input[id='partname']")
          .map(function() {
            return $(this).val();
          })
          .get();

        let message =
          "Email: " +
          email +
          "\n" +
          "Make: " +
          make +
          "\n" +
          "Model:" +
          model +
          "\n" +
          "Year:" +
          year +
          "\n" +
          "Part Name :" +
          partname;
        alert("Form submitted. We will contact you shortly ;)");
        let messageURI = encodeURI(message);
        var aTag = document.createElement("a");
        aTag.setAttribute(
          "href",
          `https://api.whatsapp.com/send?phone=+971551478994&text=${messageURI}`
        );
        window.open(aTag, "_blank").focus();
        return submitted;
      }
    });
  };

  render() {
    return (
      <div className="col-md-4 justify-content-center">
        <p className="paragraph-heading">Auto Spare Parts Inquiry Form</p>
        <hr className="py-3" />
        <iframe
          title="car battery replacement dubai"
          name="hidden_iframe"
          id="hidden_iframe"
          style={{ display: "none" }}
        ></iframe>
        <form
          id="myForm"
          method="POST"
          action="https://docs.google.com/forms/d/e/1FAIpQLSeIJu3dIAVWI2YjuO2bv31unQiJf4frrpb3IyVObr_05fmxoA/formResponse"
          target="hidden_iframe"
        >
          <div class="form-row">
            <div class="form-group col-md-6">
              <input
                type="text"
                class="form-control"
                id="whatsappno"
                name="entry.902626710"
                autocomplete="off"
                placeholder="WhatsApp No."
                required
              />
            </div>

            <div class="form-group col-md-6">
              <input
                type="email"
                class="form-control"
                id="email"
                name="entry.113755516"
                autocomplete="off"
                placeholder="Email(Optional)"
              />
            </div>
          </div>
          <hr />
          <div class="form-row">
            <div class="form-group col-md-6">
              <select
                class="form-control"
                id="year"
                name="entry.44547744"
                required="required"
                onChange={this.populateModels}
              >
                <option>Select Year</option>
              </select>
            </div>
            <div class="form-group col-md-6">
              <select
                class="form-control"
                id="make"
                name="entry.741646505"
                required="required"
                onChange={this.populateModels}
              >
                <option>Select Make</option>
              </select>
            </div>
          </div>
          <hr />
          <div class="text-row">
            <div class="form-group">
              <select
                class="form-control"
                id="model"
                name="entry.402947466"
                required
                onChange={this.populateModels}
              >
                <option value="none" disabled hidden size={5}>
                  No models found
                </option>
              </select>
            </div>
          </div>
          <hr />
          <label for="partnumber" style={{ color: "gray", fontSize: "16px" }}>
            You can add more than one Part number and Part Name
          </label>
          <div class="form-row">
            <div class="form-group col-md-10 col-xs-9 col-sm-9">
              <input
                type="text"
                class="form-control"
                id="partname"
                name="entry.1660104041"
                autocomplete="off"
                placeholder="Part Name"
                required
              />
            </div>
            <div class="form-group col-md-2 col-xs-3 col-sm-3">
              <button type="button" class="btn btn-danger haks_add">
                <span class="badge badge-danger">
                  <i class="fa fa-plus icon-plus" aria-hidden="true"></i>
                </span>
              </button>
            </div>
            <div id="haks_add_field"></div>
            <hr />
            <button
              type="submit"
              class="btn btn-danger btn-lg btn-block pb-3 form-btn"
              target="_self"
            >
              <i class="fab fa-whatsapp"></i>
              &nbsp;&nbsp;SEND
            </button>
          </div>
        </form>
      </div>
    );
  }
}
